<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="MT_table_cont">
        <!--  -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus"></el-button>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="rownum_" label="" width="100"> </el-table-column>
          <el-table-column prop="account" label=""> </el-table-column>
          <el-table-column prop="userName" label=""> </el-table-column>
          <!-- <el-table-column prop="roleNameList" label=""> </el-table-column> -->
          <el-table-column label="">
            <template slot-scope="scope">
              <el-tag >{{ scope.row.roleNameList }}</el-tag>
              <!-- <el-tag v-for="(item, index) in scope.row.roleIdList" :key="index">{{ item }}</el-tag> -->
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column label="" width="200">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="resetPass(scope.row.id)"></el-link>
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)"></el-link>
              <el-link v-if="scope.row.flag === 1" :underline="false" type="danger" @click.stop="disabled(scope.row.id, scope.row.flag)"></el-link>
              <el-link v-if="scope.row.flag === 0" :underline="false" type="primary" @click.stop="disabled(scope.row.id, scope.row.flag)"></el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)"></el-link>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!--  -->
      <el-dialog :title="type === 1 ? '' : ''" :visible.sync="dialogVisible" width="40%">
        <el-divider></el-divider>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
          <el-form-item label="" prop="account">
            <el-input v-model="ruleForm.account" placeholder=""></el-input>
          </el-form-item>

          <el-form-item label="" prop="userName">
            <el-input v-model="ruleForm.userName" placeholder=""></el-input>
          </el-form-item>

          <el-form-item v-if="type === 1" label="" prop="password">
            <el-input placeholder="" v-model="ruleForm.password" show-password></el-input>
          </el-form-item>

          <!-- <el-form-item label="" prop="roleIdList">
            <el-checkbox-group v-model="ruleForm.roleIdList">
              <el-checkbox :label="0" :disabled="isAdmin"></el-checkbox>
              <el-checkbox :label="1" :disabled="isAdmin"></el-checkbox>
              <el-checkbox :label="2" :disabled="isAdmin"></el-checkbox>
              <el-checkbox :label="4" :disabled="isDisabled"></el-checkbox>
            </el-checkbox-group>
          </el-form-item> -->
          <el-form-item label="" prop="roleIdList">
            <el-radio-group v-model="ruleForm.roleIdList">
              <!-- <el-radio label="1"></el-radio> -->
              <el-radio :label="item.id + '' " v-for="(item, index) in roleList" :key="index"> {{ item.roleName }}</el-radio>
            </el-radio-group>
          </el-form-item>
          <!-- <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
          </el-form-item> -->
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisiblePass" width="40%">
        <el-divider></el-divider>
        <!--  -->
        <set-pass @save="save" />
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import SetPass from '@/components/set-pass/setPass'
export default {
  components: { SetPass },
  data() {
    return {
      queryForm: {
        page: 1,
        size: 10,
        total: '',
        flag: [0, 1], // 1-0-2-
        userType: [0] // 0-1-2-Key()3-Key4-5-
      },
      tableData: [],
      dialogVisible: false,
      ruleForm: {
        account: '',
        userName: '',
        password: '',
        roleIdList: ''
      },
      userId: '',
      rules: {
        account: [{ required: true, message: '', trigger: 'blur' }],
        userName: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }],
        roleIdList: [{ required: true, message: '', trigger: 'blur' }]
      },
      isDisabled: false,
      isAdmin: false,
      dialogVisiblePass: false,

      roleList: [],
      type: 1 // 1 2
    }
  },

  methods: {
    // 
    add() {
      this.type = 1
      this.dialogVisible = true
      this.$nextTick(() => {
        this.$refs.ruleForm.resetFields()
        this.ruleForm.password = ''
      })
    },

    // 
    onSubmit() {
      console.log(this.queryForm)
    },

    // 
    resetPass(id) {
      this.userId = id
      this.dialogVisiblePass = true
    },

    // 
    save(e) {
      var params = {
        newPassword: this.$md5(e),
        userId: this.userId
      }
      this.httpPost('/user/resetPassword', params).then(res => {
        this.dialogVisiblePass = false
        this.$message.success('')
      })
    },

    edit(info) {
      console.log(info)
      this.type = 2
      this.dialogVisible = true
      this.$nextTick(() => {
        this.$refs.ruleForm.resetFields()
        this.ruleForm = {
          account: info.account,
          userName: info.userName,
          password: info.password,
          roleIdList: info.roleIdList,
          id: info.id
        }
        console.log(this.ruleForm)
      })
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.ruleForm.password = this.$md5(this.ruleForm.password)
          let params = Object.assign({}, this.ruleForm)
          params.roleIdList = [this.ruleForm.roleIdList]
          console.log(params)
          if (this.type === 1) {
            this.httpPost('/user/admin/add', params)
              .then(res => {
                console.log(res)
                this.$message({ type: 'success', message: '!' })
                this.loadData()
              })
              .catch(err => {
                console.log(err)
              })
          } else {
            this.httpPost('/user/admin/update/' + params.id, params)
              .then(res => {
                console.log(res)
                this.$message({ type: 'success', message: '!' })
                this.loadData()
              })
              .catch(err => {
                console.log(err)
              })
          }
          this.dialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 
    colloction(info) {
      this.$router.push({ path: '/colloction', name: 'Colloction', params: { info } })
    },

    // 
    del(id) {
      this.$delData(this, id)
    },

    // 
    disabled(id, flag) {
      console.log(id, flag)
      this.$disabledFn(this, id, flag)
    },
    loadData() {
      this.queryForm.total = ''
      this.httpPost('/user/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
      // 
      this.httpGet('/role/query', {}).then(res => {
        this.roleList = res.rows
      })
    }
  },

  // watch: {
  //   'ruleForm.roleIdList'(newVal, oldVal) {
  //     newVal.map(item => {
  //       if (item !== 4) {
  //         this.isDisabled = true
  //         this.isAdmin = false
  //       } else {
  //         this.isAdmin = true
  //         this.isDisabled = false
  //       }
  //     })
  //     if (newVal.length === 0) {
  //       this.isAdmin = false
  //       this.isDisabled = false
  //     }
  //   }
  // },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.roleNameList {
  i {
    padding: 0 5px;
  }
}
.el-tag {
  margin: 0 2px;
}
.el-radio {
  margin: 8px 20px;
}
</style>
