<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!--  -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <!-- <el-form-item label="" prop="searchType">
            <el-select v-model="queryForm.searchType" placeholder="">
              <el-option label="" value="department"></el-option>
              <el-option label="" value="userCode"></el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item label="" prop="userCode">
            <el-input v-model="queryForm.userCode" placeholder="" clearable></el-input>
          </el-form-item>
          <el-form-item label="" prop="department">
            <el-input v-model="queryForm.department" placeholder="" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit"></el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!--  -->
        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="userCode" label="" width="200"> </el-table-column>
          <el-table-column prop="department" label=""> </el-table-column>
          <el-table-column prop="deptType" label="" width="140"> </el-table-column>
          <el-table-column prop="legalPerson" label="" width="140"> </el-table-column>
          <el-table-column prop="address" label=""> </el-table-column>
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <span class="choose_btn _color" onmouseover="this.style.fontWeight='bold'" onmouseout="this.style.fontWeight='normal'" style="cursor: pointer" @click="childNum(scope.row)">
                {{ scope.row.childNumber }}
              </span>
            </template>
          </el-table-column>
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <span class="choose_btn _color" onmouseover="this.style.fontWeight='bold'" onmouseout="this.style.fontWeight='normal'" style="cursor: pointer" @click="downloadLimit(scope.row)">
                {{ scope.row.downloadLimit }}
              </span>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column label="" width="240">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="check(scope.row)"></el-link>
              <el-link :underline="false" type="primary" @click.stop="colloction(scope.row)"></el-link>
              <el-link v-if="scope.row.flag === 1" :underline="false" type="danger" @click.stop="disabled(scope.row.id, scope.row.flag)"></el-link>
              <el-link v-if="scope.row.flag === 0" :underline="false" type="primary" @click.stop="disabled(scope.row.id, scope.row.flag)"></el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)"></el-link>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!--  -->
      <el-dialog :title="label + ''" :visible.sync="dialogVisible" width="30%">
        <el-divider></el-divider>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
          <el-form-item v-if="label === ''" label="" prop="childNumber">
            <el-input v-model="ruleForm.childNumber"></el-input>
          </el-form-item>

          <el-form-item v-else label="" prop="downloadLimit">
            <el-input v-model="ruleForm.downloadLimit"></el-input>
          </el-form-item>

          <!-- <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
          </el-form-item> -->
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryForm: {
        // keywords: '',
        // searchType: 'department',
        department: '', // 
        userCode: '', // 
        page: 1,
        size: 10,
        total: '',

        flag: [0, 1], // 1-0-2-
        userType: [4] // 0-1-2-Key()3-Key4-5-
      },
      tableData: [],
      dialogVisible: false,
      ruleForm: {
        childNumber: '',
        downloadLimit: ''
      },
      label: '', //  /
      activeId: '', // Id
      rules: {
        childNumber: [{ required: true, message: '', trigger: 'blur' }],
        downloadLimit: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },

  methods: {
    // 
    onSubmit() {
      // this.queryForm[this.queryForm.searchType] = this.queryForm.keywords
      console.log(this.queryForm)
      this.loadData()
    },

    // 
    check(info) {
      this.$router.push({ path: '/childUser', name: 'ChildUser', params: { info, type: 2 } })
      this.$sessionData('set', 'type', 2)
      this.$sessionData('set', 'childInfo', info)
    },

    // 
    childNum(row) {
      console.log(row)
      this.activeId = row.id
      this.ruleForm.childNumber = row.childNumber
      this.label = ''
      this.dialogVisible = true
    },

    // 
    downloadLimit(row) {
      this.activeId = row.id
      this.label = ''
      this.ruleForm.downloadLimit = row.downloadLimit
      this.dialogVisible = true
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.label === '') {
            var params = {
              childNumber: this.ruleForm.childNumber,
              userId: this.activeId
            }
            this.httpPost('/user/childNumber/update', params)
              .then(res => {
                console.log(res)
                this.loadData()
              })
              .catch(err => {})
          } else {
            var params = {
              downloadLimit: this.ruleForm.downloadLimit,
              userId: this.activeId
            }
            this.httpPost('/user/downloadLimit/update', params)
              .then(res => {
                console.log(res)
                this.loadData()
              })
              .catch(err => {})
          }
          this.dialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 
    colloction(info) {
      this.$router.push({ path: '/colloction', name: 'Colloction', params: { info } })
    },

    // 
    del(id) {
      this.$delData(this, id)
    },

    // 
    disabled(id, flag) {
      this.$disabledFn(this, id, flag)
    },
    loadData() {
      this.queryForm.total = ''
      this.httpPost('/user/query', this.queryForm).then(res => {
        console.log(res)
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    console.log(this.$route)
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.choose_btn {
  cursor: pointer;
  text-decoration: underline;
  color: #409eff;
}
</style>
