<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!--  -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <el-form-item label="" prop="department">
            <el-input v-model="queryForm.department" placeholder="" clearable></el-input>
          </el-form-item>
          <el-form-item label="" prop="userCode">
            <el-input v-model="queryForm.userCode" placeholder="" clearable></el-input>
          </el-form-item>
          <el-form-item label="" prop="account">
            <el-input v-model="queryForm.account" placeholder="" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit"></el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!--  -->
        <div class="btns">
          <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="exportData"></span>
            </div>
          </div>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="account" label="" width="180"> </el-table-column>
          <el-table-column prop="userCode" label="" width="180"> </el-table-column>
          <el-table-column prop="department" label=""> </el-table-column>
          <!-- 0-1-2-Key()3-Key4-5- -->
          <el-table-column label="" width="180">
            <template slot-scope="scope">
              <span v-if="scope.row.userType == 0"></span>
              <span v-if="scope.row.userType == 1">2</span>
              <span v-if="scope.row.userType == 2">Key</span>
              <!-- <span v-if="scope.row.userType == 3"></span> -->
              <span v-if="scope.row.userType == 4"></span>
              <span v-if="scope.row.userType == 5"></span>
            </template>
          </el-table-column>
          <el-table-column prop="extName" label="" width="180"> </el-table-column>
          <el-table-column prop="extContract" label="" width="140"> </el-table-column>
          <el-table-column prop="extEmail" label="" width="180"> </el-table-column>
          <el-table-column prop="extUpdateTime" label="" width="180"> </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryForm: {
        department: '', // 
        userCode: '', // 
        account: '', // 
        page: 1,
        size: 10,
        total: ''
      },
      tableData: []
    }
  },

  methods: {
    // 
    onSubmit() {
      console.log(this.queryForm)
      this.loadData()
    },

    exportData() {
      let params = Object.assign({}, this.queryForm)
      let paramArray = Object.keys(params).map(key => key + '=' + params[key])
      window.open(this.$baseRequestUrl + '/user/registerUser/export?' + paramArray.join('&'))
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/user/contact/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
