<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/customUser' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!--  -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <el-form-item label="" prop="code">
            <el-input v-model="queryForm.code" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit"></el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!--  -->
        <div class="btns">
          <el-button type="primary" @click.stop="dialogVisible = true" icon="el-icon-plus"></el-button>
          <el-button type="primary" @click.stop="downModule" icon="el-icon-download"></el-button>
          <el-upload class="upload-demo" accept=".xlsx,.xls" :action="uploadAction()" :on-success="uploadSuccess" :beforeUpload="beforeExcelUpload">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text"><i class="el-icon-upload2"></i> </div>
          </el-upload>
          <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="remove"></span>
            </div>
          </div>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column prop="rownum_" label="" width="100"> </el-table-column>
          <el-table-column prop="code" label="" width="200"> </el-table-column>
          <el-table-column prop="csName" label=""> </el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <!-- {{ scope.row.status === 'A' ? '' : '' }} -->
              <el-tag v-if="scope.row.status === 'A'"></el-tag>
              <el-tag v-else type="danger"></el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="replacedSd" label=""> </el-table-column>
          <!--  -->
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)"></el-link>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!--  -->

      <el-dialog title="" :visible.sync="dialogVisible" width="30%">
        <el-divider></el-divider>

        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="code">
            <el-input v-model="ruleForm.code"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryForm: {
        code: '',
        page: 1,
        size: 10,
        total: ''
      },
      tableData: [],
      multipleSelection: [],
      dialogVisible: false,
      ruleForm: {
        code: ''
      },
      rules: {
        code: [{ required: true, message: '', trigger: 'blur' }]
      },
      info: {}
    }
  },

  methods: {
    uploadAction() {
      // return this.$picUpload
      return this.$ExcelUpload
    },

    uploadSuccess(res, file, fileList) {
      // console.log(res, file, fileList)
      this.import(res.data.fileUrl)
    },

    import(fileUrl) {
      let params = { userId: this.info.id, fileUrl }
      console.log(params)
      this.httpPost('/standard/managed/user/import', params).then(res => {
        console.log(res)
        if (res.hasManagedStandard && res.unFindStandard) {
          this.$message({ showClose: true, type: 'warning', message: '' + res.hasManagedStandard + ' \n' + '' + res.unFindStandard + '', duration: 0 })
        } else if (res.hasManagedStandard && !res.unFindStandard) {
          this.$message({ showClose: true, type: 'warning', message: '' + res.hasManagedStandard + ' \n', duration: 0 })
        } else if (!res.hasManagedStandard && res.unFindStandard) {
          this.$message({ showClose: true, type: 'warning', message: '' + res.unFindStandard + '', duration: 0 })
        }
      })
      loadData()
    },
    beforeExcelUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 100
      if (!isLt2M) {
        this.$message({
          message: ' 100MB!',
          type: 'warning'
        })
      }
      return isLt2M
    },

    // 
    downModule() {
      window.open(this.$templateDownloadUrl+'TEMPLATE1')
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = Object.assign({}, this.ruleForm)
          params.userId = this.info.id
          this.httpPost('/standard/managed/user/add', params)
            .then(res => {
              this.$message({ type: 'success', message: '!' })
            })
            .catch(() => {})
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    del(id) {
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          let params = {
            standardIdList: [id],
            userId: this.info.id
          }
          this.httpPost('/standard/managed/user/rem', params).then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.$emit('loadDate')
          })
        })
        .catch(() => {})
    },

    // 
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('')
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          let standardIdList = []
          for (var i = 0; i < this.multipleSelection.length; i++) {
            standardIdList.push(this.multipleSelection[i].id)
          }
          let params = {
            standardIdList,
            userId: this.info.id
          }
          this.httpPost('/standard/managed/user/rem', params).then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.$emit('loadDate')
          })
        })
        .catch(() => {})
    },

    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    // 
    onSubmit() {
      console.log(this.queryForm)
      this.loadData()
    },

    loadData() {
      this.queryForm.total = ''
      let params = Object.assign({}, this.queryForm)
      params.userId = this.info.id
      this.httpGet('/standard/managed/user/query', params).then(res => {
        console.log(res)
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    const { params } = this.$route
    if (params.info) {
      this.info = params.info
      console.log(this.info)
      this.loadData()
    } else {
      this.$router.replace('/customUser')
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/.upload-demo {
  display: inline-block;
  width: 60px;
  height: 30px;
  margin-left: 10px;
  .el-upload-list {
    display: none;
  }
}
/deep/.el-upload--text {
  width: 60px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 30px;
  border: 1px dashed #ccc;
  border-radius: 4px;
  .el-upload__text {
    font-size: 12px;
  }
  .el-icon-upload {
    display: none;
  }
}
</style>
