<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!--  -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <!-- <el-form-item label="" prop="pushPlat">
            <el-select v-model="queryForm.searchType" placeholder="">
              <el-option label="" value="department"></el-option>
              <el-option label="" value="account"></el-option>
              <el-option label="" value="uusername"></el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item label="" prop="account">
            <el-input v-model="queryForm.account" placeholder="" clearable></el-input>
          </el-form-item>
          <el-form-item label="" prop="userName">
            <el-input v-model="queryForm.userName" placeholder="" clearable></el-input>
          </el-form-item>
          <el-form-item label="" prop="department">
            <el-input v-model="queryForm.department" placeholder="" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit"></el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!--  -->
        <div class="btns">
          <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="remove"></span>
              <span @click.stop="exportData"></span>
            </div>
          </div>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column prop="account" label=""> </el-table-column>
          <el-table-column prop="userName" label=""> </el-table-column>
          <el-table-column prop="department" label=""> </el-table-column>
          <el-table-column prop="address" label=""> </el-table-column>
          <el-table-column prop="mobile" label="" width="140"> </el-table-column>
          <el-table-column prop="email" label="" width="180"> </el-table-column>
          <el-table-column prop="createTime" label="" width="180"> </el-table-column>
          <!--  -->
          <el-table-column label="" width="230">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="resetPass(scope.row)"></el-link>
              <el-link :underline="false" type="primary" @click.stop="check(scope.row)"></el-link>
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)"></el-link>
              <el-link v-if="scope.row.flag === 1" :underline="false" type="danger" @click.stop="disabled(scope.row.id, scope.row.flag)"></el-link>
              <el-link v-if="scope.row.flag === 0" :underline="false" type="primary" @click.stop="disabled(scope.row.id, scope.row.flag)"></el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)"></el-link>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!--  -->
      <el-dialog :title="type == 1 ? '' : ''" :visible.sync="dialogVisible" width="30%">
        <el-divider></el-divider>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="createTime">
            <el-input disabled v-model="ruleForm.createTime"></el-input>
          </el-form-item>
          <el-form-item label="" prop="account">
            <el-input disabled v-model="ruleForm.account"></el-input>
          </el-form-item>
          <el-form-item label="" prop="userName">
            <el-input :disabled="type === 1" v-model="ruleForm.userName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="department">
            <el-input :disabled="type === 1" v-model="ruleForm.department"></el-input>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input :disabled="type === 1" v-model="ruleForm.address"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input :disabled="type === 1" v-model="ruleForm.mobile"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input :disabled="type === 1" v-model="ruleForm.email"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input :disabled="type === 1" type="textarea" v-model="ruleForm.memo"></el-input>
          </el-form-item>
          <!-- <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
          </el-form-item> -->
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisiblePass" width="40%">
        <el-divider></el-divider>
        <!--  -->
        <set-pass @save="save" />
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import SetPass from '@/components/set-pass/setPass'
export default {
  components: { SetPass },
  data() {
    return {
      queryForm: {
        // keywords: '',
        // searchType: 'department',
        department: '', // 
        account: '', // 
        userName: '', // 
        page: 1,
        size: 10,
        total: '',

        flag: [0, 1], // 1-0-2-
        userType: [5] // 0-1-2-Key()3-Key4-5-
      },
      tableData: [],
      multipleSelection: [],
      dialogVisible: false,
      ruleForm: {},
      userId: '',
      type: 1, // 1 2
      rules: {
        childUser: [{ required: true, message: '', trigger: 'blur' }],
        downLimit: [{ required: true, message: '', trigger: 'blur' }]
      },
      dialogVisiblePass: false
    }
  },

  methods: {
    // 
    onSubmit() {
      // this.queryForm[this.queryForm.searchType] = this.queryForm.keywords
      console.log(this.queryForm)
      this.loadData()
    },

    // 
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('')
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          var arr = []
          this.multipleSelection.map(i => arr.push(i.id))
          console.log(arr)
          this.httpPost('/role/rem', arr).then(res => {
            this.$message({ type: 'success', message: '!' })
          })
        })
        .catch(() => {})
    },

    // 
    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    // 
    resetPass(id) {
      this.userId = id
      this.dialogVisiblePass = true
    },

    // 
    save(e) {
      var params = {
        newPassword: this.$md5(e),
        userId: this.userId
      }
      this.httpPost('/user/resetPassword', params).then(res => {
        this.dialogVisiblePass = false
        this.$message.success('')
      })
    },

    check(info) {
      this.type = 1
      this.dialogVisible = true
      this.ruleForm = Object.assign({}, info)
    },

    edit(info) {
      this.type = 2
      this.dialogVisible = true
      this.ruleForm = Object.assign({}, info)
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.type === 2) {
            this.httpPost('/user/registerUser/update/' + this.ruleForm.id, this.ruleForm).then(res => {
              this.$message.success('')
              this.loadData()
            })
          }
          this.dialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 
    update(id) {
      this.$confirm('?', '', { type: 'warning' })
        .then(() => {
          this.$message({ type: 'success', message: '!' })
        })
        .catch(() => {})
    },

    // 
    del(id) {
      this.$delData(this, id)
    },

    // 
    disabled(id, flag) {
      this.$disabledFn(this, id, flag)
    },

    // 
    exportData() {
      // /user/registerUser/export

      let params = Object.assign({}, this.queryForm)
      let paramArray = Object.keys(params).map(key => key + '=' + params[key])
      window.open(this.$baseRequestUrl + '/user/registerUser/export?' + paramArray.join('&'))
    },

    loadData() {
      this.queryForm.total = ''
      this.httpPost('/user/query', this.queryForm).then(res => {
        console.log(res)
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
