<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: type === '1' ? '/organization' : '/customUser' }">{{ type === '1' ? '' : '' }}</el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="MT_table_cont">
        <!--  -->
        <div class="btns" v-if="type === '2'">
          <el-button @click.stop="add" icon="el-icon-plus"></el-button>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="account" label="" width="200"> </el-table-column>
          <el-table-column prop="extName" label="" width="200"> </el-table-column>
          <el-table-column prop="department" label=""> </el-table-column>
          <el-table-column prop="deptType" label="" width="100">
            <template slot-scope="scope">
              <!-- {{ scope.row.flag === 1 ? '' : '' }} -->
              <el-tag v-if="scope.row.flag === 1"></el-tag>
              <el-tag type="danger" v-else></el-tag>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column label="" width="220">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="check(scope.row.id)"></el-link>
              <el-link :underline="false" type="primary" @click.stop="resetPass(scope.row.id)"></el-link>
              <el-link v-if="scope.row.flag === 1" :underline="false" type="danger" @click.stop="disabled(scope.row.id, scope.row.flag)"></el-link>
              <el-link v-if="scope.row.flag === 0" :underline="false" type="primary" @click.stop="disabled(scope.row.id, scope.row.flag)"></el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)"></el-link>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span
            class="jump_first"
            @click.stop="
              $jumpToFirst(queryForm, () => {
                loadData(type)
              })
            "
            ></span
          >
          <span
            class="jump_last"
            @click.stop="
              $jumpToLast(queryForm, () => {
                loadData(type)
              })
            "
            ></span
          >
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="
              () => {
                loadData(type)
              }
            "
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisible" width="60%">
        <el-divider></el-divider>
        <!--  -->
        <div class="MT_fn_btn">
          <el-form :inline="true" :model="queryForm2" class="btn_right demo-form-inline">
            <el-form-item label="" prop="pushPlat">
              <el-select v-model="queryForm2.logType" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
                <el-option label="" value="4"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click.stop="onSubmit"></el-button>
            </el-form-item>
          </el-form>
        </div>

        <!--  -->
        <el-table border ref="multipleTable" :data="tableData2" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="userName" label="" width="200"> </el-table-column>
          <el-table-column prop="ipAddress" label="IP" width="200"> </el-table-column>
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <span v-if="scope.row.type === 1"></span>
              <span v-if="scope.row.type === 2"></span>
              <span v-if="scope.row.type === 3"></span>
              <span v-if="scope.row.type === 4"></span>
            </template>
          </el-table-column>
          <el-table-column prop="content" label=""> </el-table-column>
          <el-table-column prop="updateTime" label="" width="180"> </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm2.total">
          <span
            class="jump_first"
            @click.stop="
              $jumpToFirst(queryForm2, () => {
                loadData(type, 1)
              })
            "
            ></span
          >
          <span
            class="jump_last"
            @click.stop="
              $jumpToLast(queryForm2, () => {
                loadData(type, 1)
              })
            "
            ></span
          >
          <el-pagination
            background
            :total="queryForm2.total"
            :current-page.sync="queryForm2.page"
            :page-size.sync="queryForm2.size"
            @current-change="
              () => {
                loadData(type, 1)
              }
            "
            layout="total, prev, pager, next, jumper"
          />
        </div>
        <span slot="footer" class="dialog-footer">
          <!-- <el-button @click="dialogVisible = false"> </el-button> -->
          <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisiblePass" width="40%">
        <el-divider></el-divider>
        <!--  -->
        <set-pass @save="save" />
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisibleChild" width="30%">
        <el-divider></el-divider>

        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="account">
            <el-input v-model="ruleForm.account"></el-input>
          </el-form-item>
          <el-form-item label="" prop="username">
            <el-input v-model="ruleForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="confirmPassword">
            <el-input type="password" v-model="ruleForm.confirmPassword" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="department">
            <el-input v-model="ruleForm.department"></el-input>
          </el-form-item>
          <el-form-item label="" prop="phone">
            <el-input v-model="ruleForm.phone"></el-input>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input v-model="ruleForm.address"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import SetPass from '@/components/set-pass/setPass'
export default {
  components: { SetPass },
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        if (this.ruleForm.confirmPassword !== '') {
          this.$refs.ruleForm.validateField('confirmPassword')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== this.ruleForm.password) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    return {
      type: '1', //  1  2
      info: {}, // 
      queryForm: {
        page: 1,
        size: 10,
        total: '',

        flag: [0, 1], // 1-0-2-
        userType: [2] // 0-1-2-Key()3-Key4-5-
      },
      queryForm2: {
        logType: '', // '' 1 2 3 4
        page: 1,
        size: 10,
        total: ''
      },
      tableData: [],
      tableData2: [],
      dialogVisible: false,
      dialogVisiblePass: false,
      dialogVisibleChild: false,
      ruleForm: {
        account: '',
        username: '',
        password: '',
        confirmPassword: '',
        department: '',
        address: '',
        phone: ''
      },
      rules: {
        account: [{ required: true, message: '', trigger: 'blur' }],
        username: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, validator: validatePass, trigger: 'blur' }],
        confirmPassword: [{ required: true, validator: validatePass2, trigger: 'blur' }],
        department: [{ required: true, message: '', trigger: 'blur' }]
      },
      userId: ''
    }
  },

  methods: {
    //  -----------------------------------------------------------------

    add() {
      this.dialogVisibleChild = true
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var params = Object.assign({}, this.ruleForm)
          params.password = params.confirmPassword = this.$md5(this.ruleForm.password)
          params.userId = this.info.id
          console.log(params)

          this.httpPost('/user/child/add', params)
            .then(res => {
              console.log(res)
              this.$message.success('')
              this.loadData(this.type)
            })
            .catch(err => {})
          this.dialogVisibleChild = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    //  -----------------------------------------------------------------

    // 
    onSubmit() {
      console.log(this.queryForm2)
      this.loadData(this.type, 1)
    },

    // 
    check(id) {
      this.userId = id
      this.queryForm2.logType = ''
      this.dialogVisible = true
      this.loadData(this.type, 1)
    },

    // 
    resetPass(id) {
      this.userId = id
      this.dialogVisiblePass = true
    },

    // 
    save(e) {
      var params = {
        newPassword: this.$md5(e),
        userId: this.userId
      }
      this.httpPost('/user/resetPassword', params).then(res => {
        this.dialogVisiblePass = false
        this.$message.success('')
      })
    },

    // 
    disabled(id, flag) {
      console.log(id, flag)
      this.$disabledFn(this, id, flag)
    },

    // 
    del(id) {
      this.$delData(this, id)
    },

    // type 1 2
    loadData(type, log) {
      if (log) {
        this.queryForm2.total = ''
        let params = Object.assign({}, this.queryForm2)
        params.userType = 1
        params.userId = this.userId
        this.httpGet('/log/user/query', params).then(res => {
          this.tableData2 = res.rows
          this.queryForm2.total = res.total
        })
      } else {
        this.queryForm.total = ''
        let params = Object.assign({}, this.queryForm)
        params.userType = [3]
        params.keyUser = this.info.id
        this.httpPost('/user/query', params).then(res => {
          this.tableData = res.rows
          this.queryForm.total = res.total
        })
      }
    }
  },

  mounted() {
    this.type = this.$sessionData('get', 'type')
    this.info = this.$sessionData('get', 'childInfo')
    this.loadData(this.type)
  }
}
</script>

<style lang="scss" scoped>
.el-form {
  margin-right: 100px;
}
</style>
